import jQuery from "jquery";
import popper from "popper.js";
// import bootstrap from "bootstrap";
import Swiper from 'swiper/bundle';
import AOS from 'aos';
import Parallax from 'parallax-js';

window.$ = jQuery;


$(document).ready(function () {
    // slider main page
    var swiper = new Swiper('.swiper-container', {
        loop: true,
        speed: 1000,
        autoplay: {
            delay: 3000,
            disableOnInteraction: false,
        },
        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        },
        pagination: {
            el: '.swiper-pagination',
            clickable: true,
        },
    });
    // main
    if ($('*').is('.mainnav')) {
        $(window).scroll(function () {
            if ($(this).scrollTop() > 10) {
                $('.mainslider .swiper-button-next, .mainslider .swiper-button-prev, .mainslider .swiper-pagination').addClass('hideel');
            } else {
                $('.mainslider .swiper-button-next, .mainslider .swiper-button-prev, .mainslider .swiper-pagination').removeClass('hideel');
            }
        });
    }
    // burger
    $('.header-icon-hamburger').click(function () {
        $('.header__menu').toggleClass('show');
        $('.header-icon-hamburger').toggleClass('burger-transform');
    });
    // concept
    $('.cval__boxdescr').click(function () {
        $(this).children('.clickhere').toggle();
        $(this).children('.descr').toggle();
    });
    if ($(window).width() > 1024) {
        let windowWidth = $(window).width(),
            containerWidth = $('.concept__topsect .container').width(),
            rightbox = (windowWidth - containerWidth) / 2;
        $('.concept__topsect .concept__topsect-pagetitle').css('left', rightbox);

        $(window).resize(function () {
            let windowWidth = $(window).width(),
                containerWidth = $('.concept__topsect .container').width(),
                rightbox = (windowWidth - containerWidth) / 2;

            $('.concept__topsect .concept__topsect-pagetitle').css('left', rightbox);
        });
    };
    $('.learnmore-text .lm').click(function () {
        $(this).hide();
        $('.learnmore-text .sl').show();
        $('.concept__topsect .concept__topsectinfo .desc-r').addClass('showt');
    });
    $('.learnmore-text .sl').click(function () {
        $(this).hide();
        $('.learnmore-text .lm').show();
        $('.concept__topsect .concept__topsectinfo .desc-r').removeClass('showt');
    });
    // VISIT US
    if ($('*').is('.swiper-containerv')) {


        var mySwiper = undefined;
        function initSwiper() {
            var screenWidth = $(window).width();
            if (screenWidth > 1024 && mySwiper == undefined) {
                mySwiper = new Swiper('.swiper-containerv', {
                    // slidesPerView: 1,
                    slidesPerView: 'auto',
                    spaceBetween: 20,
                    centeredSlides: true,
                    speed: 1000,
                    pagination: {
                        el: '.swiper-pagination',
                        clickable: true,
                    },
                    navigation: {
                        nextEl: '.swiper-button-next',
                        prevEl: '.swiper-button-prev',
                    }

                });
            } else if (screenWidth < 1025 && mySwiper != undefined) {
                mySwiper.destroy();
                mySwiper = undefined;
                jQuery('.swiper-wrapper').removeAttr('style');
                jQuery('.swiper-slide').removeAttr('style');
            }
        }

        //Swiper plugin initialization
        initSwiper();

        //Swiper plugin initialization on window resize
        $(window).on('resize', function () {
            initSwiper();
        });

    }
    // animations
    AOS.init({
        duration: 1000,
    });
    // parallax
    if ($(window).width() > 1024) {
        if ($('*').is('#scene')) {
            var scene = document.getElementById('scene');
            var parallaxInstance = new Parallax(scene, {
                relativeInput: true
            });
        }
    }



});

